@font-face {
  font-family: christmas;
  src: url("ChopinScript.4f78160f.ttf");
}

.advent-calendar-banner__big_grid {
  z-index: 3;
  grid-column-gap: 10px;
  grid-row-gap: 10px;
  max-width: 1600px;
  grid-template-rows: 1fr;
  grid-template-columns: repeat(2, 1fr);
  margin: auto;
  padding: 20px;
  display: grid;
}

.advent-calendar-banner__small_grid {
  z-index: 3;
  grid-column-gap: 5px;
  grid-row-gap: 10px;
  color: #fff;
  grid-template-rows: repeat(4, 1fr);
  grid-template-columns: repeat(6, 1fr);
  padding: 0;
  display: grid;
}

.advent-calendar-banner__present {
  text-align: center;
  border-radius: 3px;
  list-style: none;
  position: relative;
}

.advent-calendar-banner__present:hover {
  cursor: pointer;
}

.advent-calendar-banner__link {
  color: #fff;
  font-family: christmas;
  text-decoration: none;
}

.advent-calendar-banner__day {
  text-align: center;
  display: inline-block;
}

.advent-calendar-banner__present-image {
  width: 50px;
  height: 50px;
  border-radius: 25%;
  margin: 0 auto;
}

.advent-calendar-banner__present-image:hover {
  cursor: pointer;
  background: rgba(255, 255, 255, .1);
  animation: shake .5s;
  box-shadow: 0 0 5px 10px rgba(255, 255, 255, .1);
}

@media (max-width: 1200px) {
  .advent-calendar-banner__small_grid {
    z-index: 3;
    grid-column-gap: 5px;
    grid-row-gap: 10px;
    color: #fff;
    grid-template-rows: repeat(6, 1fr);
    grid-template-columns: repeat(4, 1fr);
    padding: 0;
    display: grid;
  }
}

@media (max-width: 760px) {
  .advent-calendar-banner__big_grid {
    grid-column-gap: 0px;
    grid-row-gap: 10px;
    max-width: 1600px;
    grid-template-rows: 1fr;
    grid-template-columns: repeat(1, 1fr);
    padding: 20px;
  }

  .advent-calendar-banner__small_grid {
    z-index: 3;
    grid-column-gap: 5px;
    grid-row-gap: 10px;
    color: #fff;
    grid-template-rows: repeat(4, 1fr);
    grid-template-columns: repeat(6, 1fr);
    padding: 0;
    display: grid;
  }
}

@media (max-width: 580px) {
  .advent-calendar-banner__small_grid {
    z-index: 3;
    grid-column-gap: 5px;
    grid-row-gap: 10px;
    color: #fff;
    grid-template-rows: repeat(6, 1fr);
    grid-template-columns: repeat(4, 1fr);
    padding: 0;
    display: grid;
  }
}

@keyframes shake {
  0% {
    transform: translate(1px, 1px)rotate(0deg);
  }

  10% {
    transform: translate(-1px, -2px)rotate(-1deg);
  }

  20% {
    transform: translate(-3px)rotate(1deg);
  }

  30% {
    transform: translate(3px, 2px)rotate(0deg);
  }

  40% {
    transform: translate(1px, -1px)rotate(1deg);
  }

  50% {
    transform: translate(-1px, 2px)rotate(-1deg);
  }

  60% {
    transform: translate(-3px, 1px)rotate(0deg);
  }

  70% {
    transform: translate(3px, 1px)rotate(-1deg);
  }

  80% {
    transform: translate(-1px, -1px)rotate(1deg);
  }

  90% {
    transform: translate(1px, 2px)rotate(0deg);
  }

  100% {
    transform: translate(1px, -2px)rotate(-1deg);
  }
}

